import React, { useState, useEffect } from "react";
import "./Calculator.css";

const Calculator = () => {
  const [value, setValue] = useState("");
  const [maxDisplayLength, setMaxDisplayLength] = useState(12);

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 750) {
        setMaxDisplayLength(18);
      } else {
        setMaxDisplayLength(12);
      }
    };

    // Set initial max length based on screen size
    handleResize();

    // Update max length on window resize
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const valueChange = (event) => {
    const val = event.target.value;
    if (/[\/*+-]$/.test(value) && /[\/*+-]/.test(val)) {
      return;
    }

    const newValue = value + val;
    setValue(newValue.slice(-maxDisplayLength));
  };

  const deleteChange = () => {
    const newValue = value.slice(0, -1);
    setValue(newValue.slice(-maxDisplayLength));
  };

  const clearChange = () => {
    setValue("");
  };

  const equalChange = () => {
    try {
      const result = eval(value);
      if (isNaN(result)) {
        setValue("Error");
      } else {
        setValue(result.toString().slice(-maxDisplayLength));
      }
    } catch (error) {
      setValue("Error");
    }
  };

  return (
    <div className="container">
      <div className="calculator">
        <form
          onSubmit={(event) => {
            event.preventDefault();
          }}
        >
          <div className="display">
            <input
              type="text"
              value={value}
              readOnly
              placeholder="Simplify Your Calculations."
            />
          </div>
          <div className="buttons">
            <div>
              <input type="button" value="AC" onClick={clearChange} />
              <input type="button" value="DE" onClick={deleteChange} />
              <input
                type="button"
                value="."
                className="operator"
                onClick={valueChange}
              />
              <input
                type="button"
                value="/"
                className="operator"
                onClick={valueChange}
              />
            </div>
            <div>
              <input type="button" value="7" onClick={valueChange} />
              <input type="button" value="8" onClick={valueChange} />
              <input type="button" value="9" onClick={valueChange} />
              <input
                type="button"
                value="*"
                className="operator"
                onClick={valueChange}
              />
            </div>
            <div>
              <input type="button" value="4" onClick={valueChange} />
              <input type="button" value="5" onClick={valueChange} />
              <input type="button" value="6" onClick={valueChange} />
              <input
                type="button"
                value="-"
                className="operator"
                onClick={valueChange}
              />
            </div>
            <div>
              <input type="button" value="1" onClick={valueChange} />
              <input type="button" value="2" onClick={valueChange} />
              <input type="button" value="3" onClick={valueChange} />
              <input
                type="button"
                value="+"
                className="operator"
                onClick={valueChange}
              />
            </div>
            <div>
              <input type="button" value="00" onClick={valueChange} />
              <input type="button" value="0" onClick={valueChange} />
              <input
                type="button"
                value="="
                onClick={equalChange}
                className="equal"
              />
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Calculator;
